import { tableStruct } from './interfases';
import { Order } from './TableEventsUI';
import { categoryType } from './interfases';

export const descendingComparator = (a: any, b: any, orderBy: keyof categoryType) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
};

export const getComparator = (
  order: Order,
  orderBy: any
): ((a: tableStruct, b: tableStruct) => number) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

export const stableSort = (array: tableStruct[], comparator: (a: any, b: any) => number) => {
  const stabilizedThis = array.map((el, index) => [el, index] as [any, number]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) {
      return order;
    }
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
};
