import React, { FC } from 'react';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import { grey } from '@mui/material/colors';
import { categoryType } from '../TableEvents/interfases';

interface ControlPanelProps {
  searchValue: string;
  setSearchValue: (str: string) => void;
  category: categoryType;
  setCategory: (category: categoryType) => void;
}

export const theme = createTheme({
  palette: {
    primary: {
      main: grey[900],
    },
  },
});

export const ControlPanel: FC<ControlPanelProps> = ({
  searchValue,
  setSearchValue,
  category,
  setCategory,
}) => {
  return (
    <div className="controlPanel__wrapper">
      <ThemeProvider theme={theme}>
        <FormControl sx={{ width: 160, marginRight: 2 }}>
          <InputLabel id="demo-simple-select-label"> ...</InputLabel>
          <Select
            size="small"
            labelId="demo-simple-select-label"
            id="demo-simple-select"
            value={category}
            label=" ..."
            onChange={(event) => setCategory(event.target.value as categoryType)}>
            <MenuItem value={'transport'}></MenuItem>
            <MenuItem value={'date'}></MenuItem>
            <MenuItem value={'card'}></MenuItem>
            <MenuItem value={'gasStation'}></MenuItem>
            <MenuItem value={'adress'}></MenuItem>
            <MenuItem value={'typeOfFuel'}> </MenuItem>
            <MenuItem value={'tank'}> ()</MenuItem>
            <MenuItem value={'cost'}></MenuItem>
          </Select>
        </FormControl>
        <TextField
          sx={{ width: 160 }}
          size="small"
          id="outlined-basic"
          label={
            <div className="search">
              <SearchIcon /> ...
            </div>
          }
          variant="outlined"
          type="text"
          value={searchValue}
          onChange={(e) => setSearchValue(e.currentTarget.value)}
        />
      </ThemeProvider>
    </div>
  );
};
